.suggestions {
  margin-top: 30px;
  margin-right: 20px;
}
.suggestionsTitle {
  color: grey;
  font-weight: bolder;
}
.username__left {
  display: flex;
  align-items: center;
}
.user__info {
  display: flex;
  flex-direction: column;
}
.relation {
  color: grey;
  font-size: 12px;
}
.suggestion__username {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 15px;
}
.follow_button {
  color: #0095f6;
  font-weight: bold;
  background: 0;
  border: 0;
}
.follow_button:hover {
  color: white;
  cursor: pointer;
}
.username {
  font-weight: 600;
  font-size: 14px;
}
.avatar {
  margin-right: 10px;
}
